<template>
  <div :id="id"></div>
</template>

<script>
import G2 from '@antv/g2'
import { percentFormat, floatIntFormat } from '@/utils/index'

export default {
  name: 'g2-sparkline',
  props: {
    // 
    data: {
      type: Array,
      default: () => [
        { name: '2018-01', value: 86085 },
        { name: '2018-02', value: 144776 },
        { name: '2018-03', value: 193868 },
        { name: '2018-04', value: 9616 },
        { name: '2018-05', value: 35715 },
        { name: '2018-06', value: 122503 },
        { name: '2018-07', value: 44122 },
        { name: '2018-08', value: 45153 },
        { name: '2018-09', value: 48675 },
        { name: '2018-10', value: 22159 },
        { name: '2018-11', value: 34447 },
        { name: '2018-12', value: 36865 }
      ]
    },
    // DOM 
    height: {
      type: Number,
      default: 500
    },
    // 
    axisName: {
      type: Object,
      default: () => {
        return {
          name: '',
          value: ''
        }
      }
    },
    //  area interval line
    type: {
      type: String,
      default: 'line'
    },
    // 
    color: {
      type: String,
      default: '#1890FF'
    },
    // value 
    isPercent: {
      type: Boolean,
      default: false
    },
    useTooltip: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      chart: null
    }
  },
  computed: {
    G2: function () {
      if (typeof window !== 'undefined' && window.G2) {
        return window.G2
      } else {
        return G2
      }
    }
  },
  watch: {
    // data
    data: function (val, oldVal) {
      this.drawChart(val)
    }
  },
  methods: {
    drawChart: function (data) {
      // 
      if (this.chart) {
        this.chart.destroy()
      }

      // 
      this.chart = new this.G2.Chart({
        container: this.id,
        forceFit: true,
        height: this.height,
        padding: 0
      })

      //  chart 
      this.chart.source(data)

      // 
      let _this = this
      let scaleConfig = (function () {
        let obj = {}
        for (const key in _this.axisName) {
          if (_this.axisName.hasOwnProperty(key)) {
            obj[key] = {}
            obj[key]['alias'] = _this.axisName[key]
            if (key === 'value') {
              // , (), 
              obj[key]['formatter'] = _this.isPercent ? percentFormat : floatIntFormat
            }
          }
        }
        return obj
      }())
      this.chart.scale(scaleConfig)

      // tooptip
      if (this.useTooltip) {
        // tooltip
        this.chart.tooltip(true, {
          crosshairs: {
            type: 'line'
          }
        })
      } else {
        this.chart.tooltip(false)
      }

      // 
      this.chart.legend(false)

      // 
      this.chart.axis(false)

      // 
      if (this.type) {
        this.chart[this.type]().position('name*value').color(this.color)
      }
      // 
      this.chart.render()

      // 
      this.$once('hook:beforeDestroy', function () {
        this.chart.destroy()
      })
    }
  },
  created () {
    const uuidv4 = require('uuid/v4')
    this.id = uuidv4()
  },
  mounted () {
    this.drawChart(this.data)
  }
}
</script>
