<template>
  <div :id="id"></div>
</template>

<script>
import G2 from '@antv/g2'
import { percentFormat, floatIntFormat, AxisOption } from '@/utils/index'

export default {
  name: 'g2-bubble',
  props: {
    // 
    data: {
      type: Array,
      default: () => [
        { x: 20, y: 5, size: 15, type: 'type1' },
        { x: 30, y: 10, size: 8, type: 'type2' },
        { x: 15, y: 20, size: 15, type: 'type3' }
      ]
    },
    // DOM 
    height: {
      type: Number,
      default: 300
    },
    // 
    axisName: {
      type: Object,
      default: () => {
        return {
          x: '1',
          y: '2',
          type: '',
          size: ''
        }
      }
    },
    // 
    axisColor: {
      type: Object,
      default: () => {
        return {
          lineColor: '#ccc',
          labelColor: '#999'
        }
      }
    },
    // 
    showLegend: {
      type: Boolean,
      default: true
    },
    // 
    showGrid: {
      type: Boolean,
      default: true
    },
    // 
    useTooltip: {
      type: Boolean,
      default: true
    },
    // 
    legendPosition: {
      type: String,
      default: 'bottom-center'
    },
    // 
    isPercent: {
      type: Object,
      default: () => {
        return {
          x: false,
          y: false
        }
      }
    },
    minSize: {
      type: Number,
      default: 10
    },
    maxSize: {
      type: Number,
      default: 20
    },
    // 
    padding: {
      type: Array,
      default: function () {
        return ['auto', 'auto']
      }
    }
  },
  data () {
    return {
      chart: null
    }
  },
  computed: {
    G2: function () {
      if (typeof window !== 'undefined' && window.G2) {
        return window.G2
      } else {
        return G2
      }
    }
  },
  watch: {
    // data
    data: function (val, oldVal) {
      this.drawChart(val)
    }
  },
  methods: {
    drawChart: function (data) {
      // 
      if (this.chart) {
        this.chart.destroy()
      }

      // 
      this.chart = new this.G2.Chart({
        container: this.id,
        forceFit: true,
        height: this.height,
        padding: this.padding
      })

      // 
      let _this = this
      let scaleConfig = (function () {
        let obj = {}
        for (const key in _this.axisName) {
          if (_this.axisName.hasOwnProperty(key)) {
            obj[key] = {}
            obj[key]['alias'] = _this.axisName[key]
            if (_this.isPercent[key]) {
              // 
              obj[key]['formatter'] = percentFormat
            } else {
              // 
              obj[key]['formatter'] = floatIntFormat
            }
          }
        }
        return obj
      }())
      //  chart 
      this.chart.source(data, scaleConfig)

      const defaultColorMap = ['#1890FF', '#2FC25B', '#FACC14', '#223273', '#8543E0', '#13C2C2', '#3436C7', '#F04864']
      const colorMap = Array.from(new Array(8), (v, i) => { return defaultColorMap[i] })
      let bullle = this.chart.point().position('x*y').shape('circle')

      // 
      this.chart.axis('x', new AxisOption('x', this.axisColor))
      this.chart.axis('y', new AxisOption('y', this.axisColor, this.showGrid))

      // 
      this.chart.legend('size', false)
      if (this.showLegend) {
        this.chart.legend('type', { position: this.legendPosition })
      } else {
        this.chart.legend('type', false)
      }

      // tooltip
      if (this.useTooltip) {
        // tooltip
        this.chart.tooltip({
          showTitle: false
        })
        bullle.tooltip('type*x*y')
      } else {
        this.chart.tooltip(false)
      }

      //   
      bullle.color('type', colorMap).size('size', [this.minSize, this.maxSize]).opacity(0.5)

      // 
      this.chart.render()

      // 
      this.$once('hook:beforeDestroy', function () {
        this.chart.destroy()
      })
    }
  },
  created () {
    const uuidv4 = require('uuid/v4')
    this.id = uuidv4()
  },
  mounted () {
    this.drawChart(this.data)
  }
}
</script>
